document.addEventListener("DOMContentLoaded", function() {
  // Инициализация аккордеона
  function initializeAccordion(container) {
    let accordions = container.querySelectorAll('.accordion__list');
    accordions.forEach(function(accordion) {
      new Accordion(accordion, {
        duration: 200,
        elementClass: 'accordion__item',
        triggerClass: 'accordion__top',
        panelClass: 'accordion__bottom',
      });
    });
  }

  // Переключение вкладок
  let tabs = document.querySelectorAll(".tab");
  tabs.forEach(function(tab) {
    tab.addEventListener("click", function() {
      let tabId = this.getAttribute("data-tab");

      // Удаляем класс active у всех вкладок и контента
      tabs.forEach(function(t) {
        t.classList.remove("active");
      });
      document.querySelectorAll(".tab-content").forEach(function(content) {
        content.classList.remove("active");
      });

      // Добавляем класс active к текущей вкладке и соответствующему контенту
      this.classList.add("active");
      let activeContent = document.querySelector(`.tab-content[data-tab="${tabId}"]`);
      activeContent.classList.add("active");

      // Инициализация аккордеонов для активного контента
      initializeAccordion(activeContent);
    });
  });

  // Инициализация аккордеонов для первой активной вкладки при загрузке страницы
  let initialTabContent = document.querySelector(".tab-content.active");
  if (initialTabContent) {
    initializeAccordion(initialTabContent);
  }
});
